.animate{
  -webkit-animation-name: example; /* Safari 4.0 - 8.0 */
      -webkit-animation-duration: 2s; /* Safari 4.0 - 8.0 */
      animation-name: example;
      animation-duration: 2s;
}

@-webkit-keyframes example {
    from {opacity: 0.1;}
    to {opacity: 1;}
}

/* Standard syntax */
@keyframes example {
  from {opacity: 0.1;}
  to {opacity: 1;}
}



